.row.equal-height {
    display: flex;
    flex-wrap: wrap;
}

.row.equal-height > [class*='col-'] {
    display: flex;
    flex-direction: column;
}

// GetPet font
@font-face {
    font-family: 'getpet-icons';
    src:  url('../fonts/getpet-icons.eot?z7ik8x');
    src:  url('../fonts/getpet-icons.eot?z7ik8x#iefix') format('embedded-opentype'),
      url('../fonts/getpet-icons.ttf?z7ik8x') format('truetype'),
      url('../fonts/getpet-icons.woff?z7ik8x') format('woff'),
      url('../fonts/getpet-icons.svg?z7ik8x#getpet-icons') format('svg');
    font-weight: normal;
    font-style: normal;
    font-display: block;
  }
  
  [class^="icon-"], [class*=" icon-"] {
    /* use !important to prevent issues with browser extensions that change fonts */
    font-family: 'getpet-icons' !important;
    speak: never;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
  
    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  
  .icon-globe:before {
    content: "\e903";
  }
  .icon-instagram:before {
    content: "\e904";
  }
  .icon-linkedin-square:before {
    content: "\e905";
  }
  .icon-envelope:before {
    content: "\e906";
  }
  .icon-facebook-official:before {
    content: "\e902";
  }
  .icon-chevron-right:before {
    content: "\e900";
  }
  .icon-chevron-left:before {
    content: "\e901";
  }
  